body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
  background-image: url(/Minesweeper-React/static/media/light-grey-terrazzo.37434414.png);
}

.cell {
  display: inline-block;  
  width: 30px;
  height: 30px;
  border: 1px solid black;
  box-sizing: border-box;
  padding: 7px;
  background-color: grey;
}


.boundary {
  width: 1200px;
  background-color: white;
}

* {
  margin-bottom: -5px;
  text-align: center;
  font-weight: bold;
}

.clicked {
  background-color: lightgrey;
}

.bomb.clicked {
  background-color: #DC143C;
}

.unclicked:hover {
  opacity: 0.7;
}


h1 {
  text-align: center;
}

.boundary {
  margin: 0 auto;
}

.blue {
  color: blue
}

.green {
  color: green;
}

.red {
  color:red;
}

#lose {
  margin-top: 500px;
}

button {
  margin: 0 auto;
  width: 220px;
  height: 85px;
  background-color: grey;
  color: black;
  font-size: 2em;
  margin-top: 20px;
  border: 2px solid black;
}

button:hover {
  opacity: 0.85;
  border: 4px solid black;
}

p {
  position: absolute;
}

.flag {
  background-color: orange;
}
